html, body{
  height: 100%;
  margin: 0;
  /* overflow: hidden; */
  overflow:hidden;
}

a { text-decoration: none; color:rgb(60, 60, 60);  font-weight: 700; text-align: center;} /* Globally */

/* Each state */

a:visited { text-decoration: none; color:rgb(60, 60, 60);   font-weight: 700; text-align: center;}
a:hover { text-decoration: none; color:rgb(120, 120, 120);   font-weight: 700; text-align: center;}
a:focus { text-decoration: none; color:rgb(120, 120, 120);  font-weight: 700; text-align: center; }
a:hover, a:active { text-decoration: none; color:rgb(120, 120, 120);   font-weight: 700; text-align: center;}

.App{
  background-color: rgb(250, 250, 250);
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-columns: 5fr 7fr;
  grid-template-rows:3vh 10vh 87vh;
  grid-gap: 0em;
  margin: auto;
  padding: 0em;
  position: relative;
}

.App .Title {
  background-color: rgb(172, 166, 166);
  grid-column-start: 1;
  grid-column-end: span 12;
  grid-row-start: 1;
  grid-row-end: 2;
  position: relative;
  top: 0;
  z-index: 10000;
}

.App .SearchBar {
  background-color:rgb(224, 209, 203);
  grid-column-start: 1;
  grid-column-end: span 12;
  grid-row-start: 2;
  grid-row-end: 3;
  padding-bottom: 0em;
  margin-bottom: 0em;
  position: relative;
  top: 0;
  /* position: -webkit-sticky;
  position: sticky; */
 
  z-index: 10000;
}

.App .List {
  background-color: rgb(250, 250, 250);
  grid-column-start: 1;
  grid-column-end: span 5;
  grid-row-start: 3;
  grid-row-end: 4;
  position: relative;
  z-index: 9000;
}

.App .Details {
  background-color: rgb(250, 250, 250);
  grid-column-start: 1;
  grid-column-end: span 5;
  grid-row-start: 3;
  grid-row-end: 4;
  position: relative;
  top: 0px;
  z-index: 9000;
}

.App .Map {
  grid-column-start: 6;
  grid-column-end: span 7;
  grid-row-start: 3;
  grid-row-end: 4;
  position: relative;
  z-index: 900;
}


.leaflet-container {
  height: 85vh;
  width: 60vw;
}