::selection {
  background: #ffb7b7; /* WebKit/Blink Browsers */
}

.List {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 2em;
  grid-gap: 1em;
  margin: auto;
  padding: 0em;
  height: 100%;
  width: 100%;
  overflow-x:hidden;
  overflow-y:scroll;

} 

/* The container must be positioned relative: */
.List .menu {
  grid-column-start: 3;
  grid-column-end: 4;
  width: 25%;
  right: -15rem;
  position: relative;
  color: rgb(133, 133, 133);
  font-family: Arial;
  font-weight: 600;
  border-style: none;
  float: right;
  text-align: center;
}

.List .menu select {
  display: none; /*hide original SELECT element: */
}

.List .menu .select-selected {
  background-color: rgb(224, 209, 203);
}

/* Style the arrow inside the select element: */
.List .menu .select-selected:after {
  position: absolute;
  content: "";
  top: 14px;
  right: 10px;
  width: 0;
  height: 0;
  border: 6px solid transparent;
  border-color: #fff transparent transparent transparent;
}

/* Point the arrow upwards when the select box is open (active): */
.List .menu .select-selected.select-arrow-active:after {
  border-color: transparent transparent #fff transparent;
  top: 7px;
}

/* style the items (options), including the selected item: */
.List .menu .select-items div,.select-selected {
  color: #ffffff;
  padding: 8px 16px;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer;
}

/* Style items (options): */
.List .menu .select-items {
  position: absolute;
  background-color: rgb(224, 209, 203);
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99;
}

/* Hide the items when the select box is closed: */
.List .menu .select-hide {
  display: none;
}

.List .menu .select-items div:hover, .same-as-selected {
  background-color: rgba(0, 0, 0, 0.1);
}

/* Smaller screens and tablets */
 @media only screen and (max-width: 1700px) {
  .List {
      grid-template-columns: 1fr ;
  }
}

  /* Small screens mobile */
 @media only screen and (max-width: 700px) {
  .List {
      grid-template-columns: 1fr;
  }
}  