.Details {
  position: relative;
  margin: auto;
  align-content:center;
  overflow: hidden;
  border: tomato;
  background-color: tomato;
  height: 100%;
  width: 100%;
  overflow-x:hidden;
  overflow-y:scroll;
}

.Details .Details-info {
  text-align: left;
  padding: 0 1em;
}

.Details .Details-title {
  display: inline;
  font-size: 2em;
  margin: 0;
}

.Details .Details-desc {
  display: inline;
  font-size: .8em;
  margin: 0;
}

.Details .Details-citation {
  display: inline;
  font-size: .8em;
  margin: 0;
  font-style: italic;
}


.Details .Details-image {
  position:static;
  top: 0;
}

.Details .Details-image .container .image {
  width: 100%;
}

.Details .Details-image .container .bottom-right {
  color: rgb(212, 212, 212);
  font-size: 1em;
  font-weight: 600;
  position: relative;
  bottom: 2em;
  right: 0em;
}

.Details .Details-image .container .center {
  color: rgb(212, 212, 212);
  font-size: 8em;
  font-weight: 700;
  position: relative;
  bottom: 16rem;}

.Details .Details-credit {
  display: inline;
  font-size: 1.5em;
}

