a { text-decoration: none; color:rgba(60, 60, 60, 0.7);  font-weight: 700; text-align: center;} /* Globally */

/* Each state */

a:visited { text-decoration: none; color:rgb(60, 60, 60, 0.7);   font-weight: 700; text-align: center;}
a:hover { text-decoration: none; color:rgb(120, 120, 120, 0.7);   font-weight: 700; text-align: center;}
a:focus { text-decoration: none; color:rgb(120, 120, 120, 0.7);  font-weight: 700; text-align: center; }
a:hover, a:active { text-decoration: none; color:rgb(120, 120, 120, 0.7);   font-weight: 700; text-align: center;}


.Card {
  width: 100%;
  text-align: center;
  color:rgba(60, 60, 60, 0.8);
}

.Card h2 {
  margin-top: 1rem;
  margin-bottom: .5rem;
  font-weight: 400;
}

.Card h3 {
  margin-top: -.5rem;
  margin-bottom: 0;
}

.Card > img {
  width: 100%;
  height: auto;
  position: relative;
  border-radius: 50%;
}

.container {
position: relative;
border-radius: 50%;
  }

.Card .bottom-right {
  color: rgb(212, 212, 212);
  font-size: .6em;
  font-weight: 400;
position: relative;
bottom: 2em;
right: 0em
}
.Card .center {
  color: rgb(212, 212, 212);
  font-size: 8rem;
  font-weight: 800;
position: relative;
bottom: 16rem;
}
