.SearchBar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-image: url('./navbar-image.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  height: 100%;
  width: 100%;
  color: rgb(51, 51, 51);
  text-align: center;
  /* position: center;
  box-sizing: border-box;

  display: flex;
  justify-content: center;
  align-items: baseline; */
  padding: 0em;
  margin-bottom: 0em;
}

.SearchBar h1{
  color: rgba(24, 24, 24, 0.6);
  font-weight: 400;
}

.SearchBar .SearchBar-Subtitle {
  font-size: medium;
  font-weight: 600;
  display: inline;
  color: rgba(95, 95, 95, 0.6);
}

.SearchBar .nav-link {
  display: inline-block;
  padding: 0em 1em;
  color: rgba(255, 255, 255);
  font-weight: bold;
  text-decoration: none;
}

.SearchBar .nav-link-active {
  display: inline-block;
  padding: 0em 1em;
  font-weight: bold;
  text-decoration: none;
  color: #fff;
  border-bottom: 2px solid;
}

.SearchBar .input {
  color: rgb(124, 110, 110);
  border: none;
  /* border-bottom: 2px solid rgb(129, 90, 90); */
}

.SearchBar .button {
  color: rgb(124, 110, 110);
  border: none;
  /* border-bottom: 2px solid rgb(129, 90, 90); */
}